import React from 'react';
import {Upload, Modal, Icon, Input} from 'antd';
import config from '../../utils/config';
const Ajax = require('robe-ajax')



class PicturesWall extends React.Component {
  constructor(props){
    super(props);
    const { action,name, fileList,maxFile, onListChange} = props;
    this.state = {
      action: action,
      name: name,
      previewVisible: false,
      previewImage: '',
      maxFile: maxFile || 3,
      fileList: fileList || [],
      onListChange: onListChange
    };
  }

  handleCancel = () => this.setState({ previewVisible: false })

  handlePreview = (file) => {
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  }

  handleChange = ({ file, fileList}) => {
    this.setState({ fileList: fileList})
    if(file.status === 'done' || file.status === 'removed'){
      this.state.onListChange(fileList);
    }
  }

  render() {
    const { action,name,previewVisible, previewImage, fileList,maxFile } = this.state;
    const uploadProps = {
      name: name,
      action: action || config.uploadaction,
      listType: 'picture-card',
      fileList: fileList,
      onPreview: this.handlePreview,
      onChange: this.handleChange,
      headers:{
        'Accept': 'application/json',
      },
      beforeUpload(file){
        this.data['token'] =  Ajax.ajax({
          url: config.qntoken,
          method: 'get',
          async: false
        }).responseJSON['token'];
        this.data['file'] = file.name;
        this.data['x:filename'] = file.name;
        this.data['x:size'] = file.size;
      },
    }
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text"></div>
      </div>
    );
    return (
      <div className="clearfix">
        <Upload {...uploadProps}>
          {fileList.length >= (maxFile || 3) ? null : uploadButton}
        </Upload>
        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt={previewImage} style={{ width: '100%' }} src={previewImage} />
        </Modal>
        {/*<Input type="text" name={name} value={fileList.map((item)=>item.response ? item.response.key + ',' : '')}/>*/}
      </div>
    );
  }
}

export default PicturesWall;



