import React, { PropTypes }  from 'react';
import UEditor from 'simple-react-ui';
import PictureWall from '../upload/PictureWall';
import {Form, Button, Icon,Input,Col} from 'antd';
import {downdomain, ueditorconfig, ueditorall} from '../../utils/config';

const FormItem = Form.Item;

class AddArticle extends React.Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let params = {
          articleTitle: values.articleTitle,
          articleCover: values.articleCover,
          articleContent: values.articleContent,
          id:values.id
        }
        this.props.onSubmit(params)
      }
    });
  }
  onListChange = (fileList) => {
    this.fileList = fileList;
    const {getFieldsValue, setFieldsValue, validateFields} = this.props.form;
    const fieldsValue = getFieldsValue();
    const articleCover = fileList.map((item)=>item.response.key);
    const newFieldsValue = {...fieldsValue, 'articleCover': articleCover.length > 0 ? downdomain + articleCover : ''};
    setFieldsValue(newFieldsValue);
    validateFields();
  }


  ueAfterInit = (ue) => {
    const {getFieldsValue, setFieldsValue, validateFields} = this.props.form;
    ue.addListener('contentChange', function(){
      const fieldsValue = getFieldsValue();
      const newFieldsValue = {...fieldsValue, 'articleContent': ue.getContent()};
      setFieldsValue(newFieldsValue);
      validateFields();
    });
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const {articleTitle,articleCover,articleContent,id} = this.props;
    const fileList = articleCover ? [{
      uid: -1,
      status: 'done',
      name: articleCover,
      url: articleCover
    }] : [];

    const formItemLayout = {
      labelCol: { span: 2 },
      wrapperCol: { span: 10 },
    };
    return (
      <Form onSubmit={this.handleSubmit}>

        <FormItem style={{display:'none'}}
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('id', {
            initialValue:id,
          })(<Input/>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('articleTitle', {
            initialValue: articleTitle,
            rules: [
              { required: true, message: '' },
            ],
          })(<Input/>
          )}
        </FormItem>

        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('articleCover', {
            initialValue: articleCover,
            rules: [
              { required: true, message: '' },
            ],
          })(<Input type="hidden"/>)}
          <PictureWall maxFile={1} fileList={fileList} onListChange={this.onListChange}/>
        </FormItem>

        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('articleContent', {
            initialValue: articleContent,
            rules: [
              { required: true, message: '' },
            ],
          })(<Input type="hidden"/>
          )}

          <UEditor style={{width:'100%'}} id="content" name="content"  height={500} initialContent={articleContent}
                   afterInit={this.ueAfterInit}
                   uconfigSrc={ueditorconfig}
                   ueditorSrc={ueditorall} />
        </FormItem>

        <FormItem wrapperCol={{ span: 12, offset: 6 }}>
          <Col span={6} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit"></Button>
          </Col>
        </FormItem>
      </Form>
    );
  }
}
export default Form.create()(AddArticle);



